import axios from '@/libs/api.request'

/**
 * @ http://apidoc.speiyou.cn/api/view/17337#
 *
 * 
 *
*/
export function cancel (params) {
  return axios.request({
    url: 'appointment/cancel',
    method: 'post',
    data: params
  })
}

